
@font-face {
font-family: 'Merriweather light';
    src: url('fonts/Merriweather/fonts/merriweather-light-webfont.eot');
  src: url('fonts/Merriweather/fonts/merriweather-light-webfont.eot?#iefix') format('embedded-opentype'),
       url('fonts/Merriweather/fonts/merriweather-light-webfont.woff2') format('woff2'),
       url('fonts/Merriweather/fonts/merriweather-light-webfont.woff') format('woff'),
       url('fonts/Merriweather/fonts/merriweather-light-webfont.ttf') format('truetype'),
       url('fonts/Merriweather/fonts/merriweather-light-webfont.svg#merriweatherlight') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {

    font-family: 'Merriweather lightitalic';
