       url('fonts/Merriweather/fonts/merriweather-lightitalic-webfont.woff') format('woff'),
       url('fonts/Merriweather/fonts/merriweather-lightitalic-webfont.ttf') format('truetype'),
       url('fonts/Merriweather/fonts/merriweather-lightitalic-webfont.svg#merriweatherlight_italic') format('svg');
  font-weight: normal;
  font-style: italic;
}

